.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content {
  margin: 1em 2em;
}

.yukleme {
  margin: 0em 2em;
  font-size: 2.5em;
  font-style: bold;
}

.logo {
  height: 4em;
  margin: 1em 2em;
}

.submit {
  margin: 0em 2em;
  font-size: 1.5em;
  font-style: bold;
}

